import React, { useEffect, useState } from 'react';

import Container from 'react-bootstrap/Container';
import Alert from 'react-bootstrap/Alert';

import { connect, useSelector } from 'react-redux';

import { fetchAPI, selectOrarioGiorno, selectOrarioSettimana, selectOrarioSuccessivo, selectOrarioSettimanaProssima } from '../redux/data';
import { Table, Button } from 'react-bootstrap';

import CategorieLezioni, { withCategorieLezioni } from '../context/categorieLezioni';

import moment from 'moment';

const sendNotification = async (config) => {
  return await window.showNotificationWithTrigger(config);
}

const remind = async (arrayLezioni) => {
  let notificationCount = 0;

  for (let i = 0; i < arrayLezioni.length; i++) {
    const g = arrayLezioni[i];
    const [ore, minuti] = g.inizio.split(":");

    const timestamp = moment(g.data).add(ore, 'hours').add(minuti - 5, 'minutes').unix();
    const config = {
      tag: timestamp, // a unique ID
      body: `Lezione di ${g.nome_insegnamento} alle ${ore}:${minuti}`, // content of the push notification
      badge: './logo192.png',
      icon: './logo192.png',
    };
    let ok = await sendNotification(config);
    if (ok) {
      notificationCount++;
    }
  }

  return [notificationCount, arrayLezioni.length];
}

const ContainerOrari = ({ isDataLoaded, fetchAPI }) => {
  const [snackbar, setSnackbar] = useState({
    message: undefined,
    type: undefined
  });

  const [cancelId, setCancelId] = useState(undefined);

  const giorno = useSelector(selectOrarioGiorno);
  const settimana = useSelector(selectOrarioSettimana);
  const settimanaProssima = useSelector(selectOrarioSettimanaProssima);
  const altro = useSelector(selectOrarioSuccessivo);

  const handleSnackbarClose = () => {
    setSnackbar({
      message: undefined,
      type: undefined
    });
    setCancelId(undefined);
  };

  useEffect(() => {
    if (!isDataLoaded)
      fetchAPI();
  }, [isDataLoaded, fetchAPI]);

  useEffect(() => {
    if (snackbar.message) {
      if (cancelId) clearTimeout(cancelId);
      const id = setTimeout(handleSnackbarClose, 2500);
      setCancelId(id);
    }
    // eslint-disable-next-line
  }, [snackbar])

  const sendReminder = (array) => {
    remind(array).then(([sent, total]) => {
      let type;
      if (sent === 0) type = 'danger';
      else if (sent > 0 && sent < total) type = 'warning';
      else type = 'success';

      setSnackbar({
        message: `Programmati ${sent}/${total} promemoria`,
        type
      });
    });
  }

  return (
    <Container className="mt-3">
      {
        isDataLoaded ? (
          <>
            <Alert show={snackbar.type !== undefined} variant={snackbar.type}>
              <p>
                {snackbar.message}
              </p>
            </Alert>

            <div className="d-flex justify-content-between align-items-center">
              <h1>Lezioni di oggi</h1>
              <Button onClick={() => {
                sendReminder(giorno);
              }}>Ricordamelo</Button>
            </div>
            {
              giorno && giorno.length > 0 ? (
                <TabellaOrario orari={giorno} />
              ) : (
                  <div>Non ci sono lezioni per oggi</div>
                )
            }
            <hr />

            <div className="d-flex justify-content-between align-items-center">
              <h2>Lezioni della settimana</h2>
              <Button onClick={() => {
                sendReminder(settimana);
              }}>Ricordamelo</Button>
            </div>
            {
              settimana && settimana.length > 0 ? (
                <TabellaOrario orari={settimana} />
              ) : (
                  <div>Non ci sono altre lezioni per questa settimana</div>
                )
            }
            <hr />

            <div className="d-flex justify-content-between align-items-center">
              <h2>Lezioni settimana prossima</h2>
              <Button onClick={() => {
                sendReminder(settimanaProssima);
              }}>Ricordamelo</Button>
            </div>
            {
              settimanaProssima && settimanaProssima.length > 0 ? (
                <TabellaOrario orari={settimanaProssima} />
              ) : (
                  <div>Non ci sono lezioni per settimana prossima</div>
                )
            }
            <hr />

            <h3>Lezioni successive</h3>
            {
              altro && altro.length > 0 ? (
                <TabellaOrario orari={altro} />
              ) : (
                  <div>Non ci sono altre lezioni</div>
                )
            }
          </>
        ) : (
            <h1>Caricamento...</h1>
          )
      }
    </Container>
  )
};

const TabellaOrario = withCategorieLezioni(({ orari, categorieLezioni }) => (
  <Table striped bordered hover responsive>
    <thead>
      <tr>
        <th>Data</th>
        <th>Insegnamento</th>
        <th>Orario</th>
        <th>Modalit√† lezione</th>
      </tr>
    </thead>
    <tbody>
      {
        orari.map((o, i) => <Item key={i} orario={o}
          categoriaSelezionata={categorieLezioni.getTipologia(o.id_insegnamento)}
          handleChangeCategoria={(v) => categorieLezioni.setData(o.id_insegnamento, v)} />)
      }
    </tbody>
  </Table>
));

const Item = ({ orario: { data, nome_insegnamento, inizio, fine }, categoriaSelezionata, handleChangeCategoria }) => (
  <tr>
    <td>{data && data.format("DD/MM/YYYY")}</td>
    <td>{nome_insegnamento}</td>
    <td>{inizio} - {fine}</td>
    <td>
      <select value={categoriaSelezionata} onChange={({ target: { value } }) => {
        handleChangeCategoria(value);
      }}>
        {
          CategorieLezioni.values.map((c, i) => (
            <option key={i} value={i}>{c}</option>
          ))
        }
      </select>
    </td>
  </tr>
);

const mapStateToProps = (state => {
  return { isDataLoaded: state != null }
});

export default connect(mapStateToProps, { fetchAPI })(ContainerOrari);
